.card {
    /* Add shadows to create the "card" effect */
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    width: 230px;
    background-color: #f6fcff;
    border-radius: 2px;
    margin-left: 12px;
    margin-top: 12px;
    border-radius: 10px;
}


/* On mouse-over, add a deeper shadow */

.card:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.card-header {
    display: flex;
    font-size: small;
    justify-content: space-between;
    background-color: rgba(240, 239, 255, 255);
    width: 100%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.card-body {
    font-size: small;
    padding: 6px 20px;
    line-height: 15px;
    /* width: 200px; */
    /* text-align: justify; */
}

.long-card {
    width: 472px !important;
}